@import '../../../../styles/variables.scss';
.solution {
    padding: 100px 140px;
    display: flex;
    flex-direction: column;
    align-items: center;
  
    h2 {
      font-weight: 500;
      font-size: 42px;
      line-height: 70px;
      color: #3e3e3e;
    }
    &-list {
      ul {
        margin-top: 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        li {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          width: 25%;
          h3 {
            font-weight: 500;
            font-size: 20px;
            line-height: 32px;
            color: #3d3d3f;
          }
          p {
            margin-top: 20px;
            font-weight: 400;
            font-size: 12px;
            line-height: 20px;
            text-align: center;
            color: rgba(0, 0, 0, 0.5);
          }
          .item {
            height: 80px;
            width: 80px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: grey;
            box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.1);
            img {
              height: 50px;
              width: 50px;
            }
          }
  
          .item-1 {
            background: linear-gradient(
              180deg,
              rgba(0, 158, 255, 0.1) 0%,
              rgba(56, 175, 255, 0.1) 100%
            );
          }
          .item-2 {
            background: radial-gradient(
              51.35% 53.44% at 48.82% 46.56%,
              rgba(255, 170, 6, 0.12) 0%,
              rgba(255, 180, 26, 0.12) 100%
            );
          }
          .item-3 {
            background: linear-gradient(
              180deg,
              rgba(0, 158, 255, 0.25) 0%,
              rgba(56, 175, 255, 0.25) 100%
            );
          }
        }
      }
    }
  }